<template>
  <Top></Top>
  <div class="content">
    <div class="page_list">
      <div :class="{ 'item': true, 'choosed': choosedPageName === page.name }" v-for="page in pageList" :key="page.name"
        @click="onChoosePage(page)">
        <img class="icon" :src="page.src" :alt="page.name">
        {{ page.name }}
      </div>

    </div>
    <div class="page">
      <component :is="thePage"></component>
    </div>
  </div>
</template>

<script setup>
import Top from './components/Top.vue'
// import Topic from './components/Topic.vue'
import Home from './pages/home.vue'
import Member from './pages/member.vue'
import Category from './pages/category.vue'
import AboutUs from './pages/about_us.vue'


import { ref } from "vue"

import homeImage from "./assets/images/icon/home.svg"
import categoryImage from "./assets/images/icon/category.svg"
import memberImage from "./assets/images/icon/member.svg"

import aboutUsImage from "./assets/images/icon/about_us.svg"

import vipImage from "./assets/images/icon/vip.svg"


const thePage = ref(Home)


const pageList = [
  {
    name: "ForumHomePage",
    src: homeImage,
    component: Home,
  },
  {
    name: "ForumClassification",
    src: categoryImage,
    component: Category,
  },
  {
    name: "ForumMember",
    src: memberImage,
    component: Member,
  },
  {
    name: "About",
    src: aboutUsImage,
    component: AboutUs,
  },
  {
    name: "AdminOnly",
    src: vipImage,
    component: Category,
  },
]

// The selected page name
const choosedPageName = ref(pageList[0].name)

function onChoosePage(page) {
  console.log('page', page)
  choosedPageName.value = page.name
  thePage.value = page.component
}

</script>

