export class Topic {
    constructor(image, followCount) {
        this.image = image
        this.followCount = followCount
    }
}

import aImage from "../assets/images/4a6a714b141a8d2bee058d47c0a4702.jpg"
import bmage from "../assets/images/028b5dc489ada80228d04c71cfa0975.png"

import cImage from "../assets/images/1778b7d044ba70b8b3cf35f6a0e8b63.jpg"
import dImage from "../assets/images/a093cb7e711667cf2786c238e58f19e.png"
import eImage from "../assets/images/c68e43db8acec1e8184b759bf02efe3.png"
import fImage from "../assets/images/inbound.jpg"

export function getRecommendTopicList() {
    const testTopicImages = [aImage, bmage, cImage, dImage, eImage, fImage]
    const recommendTopicList = new Array(testTopicImages.length).fill(666).map((_, i) => {
        return {
            image: testTopicImages[i],  
            name: 'test',               
            followCount: i * 10,        
            id: 'B' + i                 
        }
    })

    return recommendTopicList
}